// --------------------------
// Core Imports
// --------------------------

@import "../../../components/miyagi-core/styles/variables";
@import "../../../components/miyagi-core/styles/mixins";
@import "../../../components/miyagi-core/styles/typography";

// --------------------------
// Components
// --------------------------

@import "../../../components/cards/styles/index";

// --------------------------
// Styles
// --------------------------

.product-content {
    margin-top: 3rem;
    margin-bottom: 4rem;

    @include bp(tab-up) {
        margin-top: 4.1rem;
        margin-bottom: 6rem;
    }
}

.card-group--partners {
    &:not(:last-of-type) {
        margin-bottom: 7.5rem;

        @include bp(tab-up) {
            margin-bottom: 4.8rem;
        }
    }
}

.card-group--partners__name {
    @extend %primary-font-light;
    color: palette(sanMarino, base);
    font-size: 2.8rem;
    letter-spacing: 0.02rem;
    line-height: (28 / 28);
    margin-bottom: 1.6rem;

    @include bp(tab-up) {
        margin-bottom: 2.8rem;
    }
}

.card-group--partners__description {
    @extend %primary-font-light;
    color: map-get($color, action-dark-54);
    font-size: 1.8rem;
    letter-spacing: 0.02rem;
    line-height: (28 / 18);
    margin-top: 3.2rem; // Collapses the name elements margin and uses this instead (if description element present).

    // Overriding paragraph defaults.
    &,
    &:last-of-type {
        margin-bottom: 1.3rem;

        @include bp(tab-up) {
            margin-bottom: 4.5rem;
        }
    }
}